import {Component, input} from '@angular/core';
import {FineDineEntity, Note} from "../../types/fineDineEntity";
import {CocktailComponent} from "../cocktail/cocktail.component";
import {fadeInOut} from "../../animations";

@Component({
  selector: 'demo-section',
  standalone: true,
  imports: [
    CocktailComponent
  ],
  animations: [fadeInOut()],

  template: `
    <div @fadeInOut id="{{getName('de')}}" class="w-full    relative h-screen">
      <div class="sticky z-[9999] bg-white top-0 left-0 px-5 drop-shadow-2xl ">
      <h1 class="text-xl mt-2">{{getName()}}</h1>
      <p class="text-md ml-5 pb-2">{{getDescription()}}</p>
      </div>
      <div class="w-full px-5 mt-5 grid py-2 place-items-center md:grid-cols-3 grid-cols-1 gap-5">
        @for(entity of matchingEntities; track entity._id; let i = $index) {
          <demo-cocktail [entity]="entity"></demo-cocktail>
        }
      </div>
    </div>
  `,
  styles: `
  :host {
    @apply w-full h-full;
  }`
})
export class SectionComponent {
  section= input.required<FineDineEntity>();
  allData= input.required<FineDineEntity[]>();

  getName(lang: 'de' | 'en' = 'de') {
    return typeof this.section().name === 'string' ? this.section().name : (this.section().name as Note)[lang]
  }
  getDescription(lang: 'de' | 'en' = 'de') {
    return typeof this.section().description === 'string' ? this.section().description : (this.section().description as Note)[lang]
  }

  get matchingEntities() {
    return this.allData().filter(e => e.parentId === this.section()._id)
  }
}
